.container{
    color: white;
}
h1{
    font-size: 38px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 600;
    text-shadow:2px 2px 5px rgb(17, 16, 16)  ;
    letter-spacing: 2px;
}
p{
    font-size: 20px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 600;
    text-shadow:2px 2px 5px rgb(17, 16, 16)  ;
    letter-spacing: 2px;
}
.temp{
    height: 30vh;
    width:50%;
    margin:25px auto;
    border-radius: 10px;
    background-color: rgba(8, 52, 119, 0.6) ;
    box-shadow:2px 6px 10px rgba(17, 16, 16, 0.9) ;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
h2{
    font-size: 50px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    letter-spacing: 5px;
    text-shadow:2px 2px 5px rgb(17, 16, 16)  ;
}
.temp p{
    font-size: 50px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 600;
    text-shadow:2px 2px 5px rgb(17, 16, 16)  ;
    letter-spacing: 2px;
    margin-top: -10px;
}

