.App {
  height: 100vh;
  width: 100%;
  text-align: center;
  background-color: rgb(68, 67, 67);
  display: flex;
}
.container{
  height: 90vh;
  width:80%;
  margin: auto;
  border-radius: 10px;
  background-image: url("https://media.nbclosangeles.com/2019/09/27c456e1012347d982b088d2c78cf58d.jpg?fit=4160%2C3120");
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.8), 0 7px 22px 0 rgba(0, 0, 0, 0.8);
}
form{
  margin:25px auto ;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
input[type="text"]{
  height:35px;
  width: 60%;
  padding: 0px 25px;
  border-radius: 5px;
  border: none;
  box-shadow:2px 2px 5px rgba(17, 16, 16, 0.9) ;
  font-size: 15px;
  color: tomato;
}
input::placeholder{
  font-size: 15px;
  color:tomato;
}
input[type="text"]:focus,input[type="submit"]:focus{
  outline: none;
  box-shadow:2px 6px 10px rgba(17, 16, 16, 0.9) ;
}
input[type="submit"]{
  width: 150px;
  height:35px;
  margin: 20px 0px;
  border-radius:5px;
  background-color: tomato;
  border-color: tomato;
  color: white;
  font-size: 15px;
  box-shadow:2px 2px 5px rgba(17, 16, 16, 0.9) ;
}
input[type="submit"]:hover{
  cursor: pointer;
  background-color: tomato;
  transition:0.3s ;
  box-shadow:2px 2px 5px rgba(17, 16, 16, 0.9) ;
}